import React from 'react';
import './App.css';

// App Custom Parts
import SculisAppBar from './app/components/AppBar';
import SculisBody from './app/components/AppBody';
import SculisBottomNavigation from './app/components/AppNavi';

//MUI Themes
import { sculisLightTheme } from './app/themes/Pink';
import { ThemeProvider } from '@mui/material/styles';

// Types
export type SculisScreen = "dashboard-screen" | "schedule-screen" | "substitution-screen"

// Main Part
export default function App() {
  const [screen, setScreen] = React.useState<SculisScreen>("dashboard-screen")
  return (
    <SculisAppContainer>
      <SculisAppBar forScreen={screen} />
      <SculisBody forScreen={screen} />
      <SculisBottomNavigation forScreen={screen}
        // Callbacks (screen change)
        dashboardScreenCallback={() => { setScreen("dashboard-screen") }}
        scheduleScreenCallback={() => { setScreen("schedule-screen") }}
        substitutionScreenCallback={() => { setScreen("substitution-screen") }}
      />
    </SculisAppContainer>
  );
}


function SculisAppContainer(props: any) {
  return (
    <ThemeProvider theme={sculisLightTheme}>
      <div className='fixed
        w-full h-full
        flex flex-col
        select-none'>
        {props.children}
      </div>
    </ThemeProvider>
  )
}
