const StandinNews = () => {
    const news = [
        "Schulschluss nach der 3. Stunde!",
        "Sekundarstufe 1 hat Unterrichtsentfall nach der 1. Stunde.",
        "Biologie, Chemie und Informatik Klasuren  verschoben in E14!"
    ]
    return (
        <Card sx={{ borderRadius: '25px', marginY: "20px", border: "none" }} variant="outlined">
            <CardHeader
                title={<Typography fontSize="18px"
                    textAlign="center"
                    fontWeight="bold"
                >
                    {"Nachrichten"}

                </Typography>}

                disableTypography
                contentEditable={false}
            />
            < CardContent >
                {news.map(function (value, index, arr) {
                    return (
                        <div>
                            <div style={{ display: "flex" }}>
                                <Typography sx={{ marginRight: "10px" }}>
                                    {" § "}
                                </Typography>
                                <Typography variant="body1">
                                    {value}
                                </Typography>
                            </div>
                            <Divider />
                        </div>

                    )
                })
                }
            </CardContent >
        </Card >
    )
}

