import React from 'react';

import { AppBar, Container } from '@mui/material';

import Substitution from './Substitution';
import Dashboard from './Dashboard';
import Schedule from './Schedule';

import { SculisScreen } from '../../../App';



type SculisAppBarProps = {
    forScreen: SculisScreen
}

export default function SculisAppBar(props: SculisAppBarProps) {

    return (
        <Container disableGutters>
            <AppBar position="static">
                {
                    props.forScreen === 'dashboard-screen' ? (
                        <Dashboard />
                    ) : props.forScreen === 'schedule-screen' ? (
                        <Schedule />
                    ) : props.forScreen === 'substitution-screen' ? (
                        <Substitution />
                    ) : (
                        null
                    )
                }
            </AppBar>
        </Container>
    )
};


