import { alpha, createTheme } from "@mui/material/styles";

declare module '@mui/material/styles' {
    interface Theme {
        status: {
            danger: string;
        };
        custom: any;
    }
    // allow configuration using `createTheme`
    interface ThemeOptions {
        status?: {
            danger?: string;
        };
        props?: any;
        custom: any;
    }
    interface Components {
        MuiPickerStaticWrapper?: any;
        MuiCalendarPicker?: any;
        MuiPickersDay?: {
            gradients: any;
        };
    }
}

let palette = createTheme({
    palette: {
        mode: "dark",
        primary: {
            main: '#e84b4d',
        },
        secondary: {
            main: '#ffffff',
        },
        error: {
            main: '#bd1d11',
        },
        info: {
            main: "#ffffff",
        },
    },
    custom: {
        gradients: {
            button: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)'
        }
    }
});


export let sculisLightTheme = createTheme(palette, {
    typography: {
        button: {
            letterSpacing: '0.19em',
            fontSize: '0.82rem',
        },
    },
    components: {
        MuiPickerStaticWrapper: {
            styleOverrides: {
                root: {
                    "& > div >  div:not(.PrivatePickersToolbar-root)": {
                        margin: 0,
                        width: "100%"
                    },
                },
            }
        },
        MuiCalendarPicker: {
            styleOverrides: {
                root: {
                    "& div": {
                        justifyContent: "space-between"
                    },
                    "& > :last-child": {
                        margin: "0 12px",
                    },
                    margin: 0,
                    width: "100%",
                    maxWidth: "100%",
                    color: palette.palette.text.secondary
                },
            },
        },
        MuiSwitch: {
            styleOverrides: {
                root: {
                    width: 46,
                    height: 26,
                    padding: 0,
                    margin: 0,
                },
                switchBase: {
                    padding: 1,
                    '&$checked, &$colorPrimary$checked, &$colorSecondary$checked': {
                        transform: 'translateX(16px)',
                        color: '#fff',
                        '& + $track': {
                            opacity: 1,
                            border: 'none',
                        },
                    },
                },
                thumb: {
                    width: 24,
                    height: 24,
                },
                track: {
                    borderRadius: 13,
                    border: '1px solid' + palette.palette.getContrastText(palette.palette.background.default),
                    backgroundColor: palette.palette.background.default,
                    opacity: 1,
                    transition: 'background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
                },
            }

        },
        MuiButton: {
            styleOverrides: {
                root: {
                    // background: palette.custom.gradients.button,
                    border: 0,
                    borderRadius: 999,
                    // boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                    // color: 'white',
                    height: "auto",
                    padding: '5px 15px',
                },
            }

        },
        MuiContainer: {
            styleOverrides: {
                root: {
                    maxWidth: 'sm'
                }
            }
        },
        MuiBottomNavigation: {
            styleOverrides: {
                root: {
                    width: "100%"
                }
            }
        },
        MuiMenu: {
            styleOverrides: {
                root: {
                    "& .MuiPaper-root ": {
                        backgroundImage: "none",
                        border: "none",
                        boxShadow: "none",
                        backgroundColor: "transparent"
                    }

                }
            }
        }
    },
    props: {
        MuiAppBar: {
            color: 'transparent',
        },
        MuiTooltip: {
            arrow: true,
        },
    },
});