import React from 'react';

// MUI
import { Container, Toolbar, IconButton, Button } from "@mui/material";
import { RowListExtended } from "../../Commons/StyleOverrides";

// Icons
import MenuIcon from '@mui/icons-material/Menu';
import FilterListRoundedIcon from '@mui/icons-material/FilterListRounded';
import NotificationsRoundedIcon from '@mui/icons-material/NotificationsRounded';

//
import SubBar, { SubBarType } from './SubBar';


export default function Substitution() {
    // common states
    const [date, setDate] = React.useState<Date>(new Date());
    const [subbar, setSubBar] = React.useState<SubBarType>(null);

    const handleSubBarChange = (pSubBar: SubBarType) => {
        if (subbar === pSubBar) {
            setSubBar(null);
        } else {
            setSubBar(pSubBar);
        }
    };

    function formatDate(date: Date) {
        return date.toLocaleString('de-DE', {
            weekday: "short",
            month: "short",
            day: "numeric"
        })
    }

    return (
        <Container disableGutters>
            {/* APPBAR */}
            <Toolbar sx={{ justifyContent: "space-between" }}>
                <RowListExtended>
                    <IconButton children={<MenuIcon />}
                        edge="start"
                        color="inherit"
                        onClick={() => { }}
                    />
                    <Button
                        variant="contained"
                        onClick={() => handleSubBarChange('date')}
                        sx={{
                            marginLeft: "10px"
                        }}
                    >
                        {
                            formatDate(date)
                        }
                    </Button>
                </RowListExtended>
                <RowListExtended>
                    <IconButton children={<FilterListRoundedIcon />}
                        edge="start"
                        color="inherit"
                        onClick={() => handleSubBarChange('filter')}
                    />
                    <IconButton
                        edge="end"
                        color="inherit"
                        onClick={() => handleSubBarChange('notification')}
                        children={<NotificationsRoundedIcon />}
                    />
                </RowListExtended>
            </Toolbar>
            <SubBar
                date={date}
                // callbacks
                handleDateChange={(pDate: Date) => setDate(pDate)}
                state={subbar}
            />
        </Container>
    )
}

